@import './styles/reset.scss';
@import './styles/fuse-grid.scss';
@import './styles/modals.scss';
@import './styles/info-sidebar.scss';

body {
    background-image: url("../images/background.jpeg");
    font-family: 'Nova Flat';
}

.game-space {
    width: 60%;
    margin: 0 auto;
}

h1 {
    padding: 10px;
    margin: 20px;
    text-align: center;
    background-image: url("../images/marquee.jpeg");
    background-position: center;
    background-repeat: no-repeat;
}

.game-title {
    font-size: 52;
    font-weight: bolder;
    color: black;
    padding: 10px;
}

img {
    height: 50px;
    width: 50px;
}

#game-components {
    display: flex;
    justify-content: space-evenly;
}

.game-board {
    display: flex;
    align-items: center;
    justify-content: center;
    // background-color: #570000;
    background-color: #7c0000;
    border-radius: 5px;
    padding: 10px 5px 10px 5px;
}

li {
    border: 1px solid transparent;
    padding: 1px;
}

.kitten-and-bowl {
    float: right;
    padding-right: 50px;
    position: relative;
}


#kitten, #bowl {
    position: absolute;
    right: 160px;
}

#kitten {
    width: 150px;
    height: 200px;
    top: -100px;
}

#bowl {
    width: 150px;
    height: 150px;
    top: -25px;
}


.kernel-img {
    padding: 1px 3px 1px 5px;
}


// pulsating flames
.pulsing {
    transform: scale(1);
	animation: pulse 1s infinite;
}

@keyframes pulse {
    0%{transform: scale(0.8)}
    60%{transform: scale(1.0)}
    100%{transform: scale(0.8)}
}

// curtains drawn before game starts
.curtain {
    z-index: 997;
    position: absolute;
    height: 100%;
    width: 50%;
    top: 0px;
}

.right {
    margin-left: 50%;
}

@keyframes lightSpeedOutLeft {
   0% { 
      opacity: 1; 
   }
   100% { 
      transform: translateX(-100%) skewX(30deg); 
      opacity: 0.5; 
   } 
} 

.lightSpeedOutLeft {
  animation-name: lightSpeedOutLeft;
  animation-timing-function: ease-in;

  animation-duration: 1s;
  animation-fill-mode: both;
} 

@keyframes lightSpeedOutRight {
   0% { 
      opacity: 1; 
   }
   100% { 
      transform: translateX(100%) skewX(-30deg); 
      opacity: 0; 
   } 
} 

.lightSpeedOutRight {
  animation-name: lightSpeedOutRight;
  animation-timing-function: ease-in;

  animation-duration: 1s;
  animation-fill-mode: both;
} 


// popped popcorn animation
canvas {
    z-index: 1;
    position: absolute;
    right: 11%;
}

.bottom-border {
    background-color: black;
    height: 200px;
    width: 100%;
    bottom: -40px;
}